.hero-cursor {
  position: absolute;
  width: 240px;
  height: 240px;
  top: 0;
  left: 0;
  background: #ffe8e8;
  border-radius: 80%;
  border: 160px solid #fe1000b8;
  backface-visibility: hidden;
  pointer-events: none;
  z-index: 10;
  transition: 0.5s all linear; }

section.hero-scene {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  height: 100vh;
  width: 100vw;
  z-index: 1; }
  section.hero-scene .mobile-only {
    display: none; }
  section.hero-scene h1 {
    font-size: 130px;
    margin-top: 0;
    margin-bottom: 0; }
    section.hero-scene h1.first {
      margin-bottom: -70px; }
  section.hero-scene .skillset {
    position: absolute;
    bottom: 2vh;
    left: 0;
    list-style: none;
    text-transform: uppercase; }
    section.hero-scene .skillset .tools {
      font-size: 12px; }
  section.hero-scene .availability {
    position: absolute;
    height: 100vw;
    width: 100vh;
    padding-left: 6%;
    padding-bottom: 4%;
    display: flex;
    align-items: flex-end;
    font-size: 16px;
    letter-spacing: 5px;
    text-transform: uppercase;
    transform: rotate(-90deg); }

@media screen and (max-width: 728px) {
  section.hero-scene .mobile-only {
    display: unset; }
  section.hero-scene h1 {
    font-size: 48px; }
  section.hero-scene h1.first {
    margin-bottom: -23px; }
  section.hero-scene .skillset {
    left: -15px; }
  section.hero-scene .availability {
    display: none; } }
