.project-card {
  position: relative;
  text-transform: uppercase;
  font-family: "Montserrat", sans-serif;
  font-weight: 900;
  width: 100%;
  height: 70vh;
  display: flex;
  flex-direction: column; }
  .project-card .fa-external-link-alt {
    display: none; }
  .project-card img {
    position: absolute;
    height: 80%;
    width: auto;
    top: -50px;
    opacity: 0; }
  .project-card.rafaelbolano, .project-card.foodlab, .project-card.otaku {
    align-items: flex-end;
    right: -50vw; }
    .project-card.rafaelbolano img, .project-card.foodlab img, .project-card.otaku img {
      left: 5vw; }
    .project-card.rafaelbolano h3, .project-card.rafaelbolano h4, .project-card.foodlab h3, .project-card.foodlab h4, .project-card.otaku h3, .project-card.otaku h4 {
      margin-right: 5vw; }
  .project-card.zest, .project-card.santorini, .project-card.thatsmyrock {
    align-items: flex-start;
    left: -50vw; }
    .project-card.zest img, .project-card.santorini img, .project-card.thatsmyrock img {
      right: 5vw; }
    .project-card.zest h3, .project-card.zest h4, .project-card.santorini h3, .project-card.santorini h4, .project-card.thatsmyrock h3, .project-card.thatsmyrock h4 {
      margin-left: 5vw; }
  .project-card h3 {
    font-size: 100px;
    z-index: 999; }
  .project-card h4 {
    margin-top: -100px; }

.transition-project-on {
  animation: 2s fadeInProject;
  animation-delay: 0.2s;
  animation-fill-mode: forwards; }

@keyframes fadeInProject {
  from {
    margin-left: 10vw;
    margin-right: 10vw;
    opacity: 0; }
  to {
    margin-left: 0;
    margin-right: 0;
    opacity: 0.5; } }

@media screen and (max-width: 728px) {
  .project-card {
    height: 40vh; }
    .project-card .fa-external-link-alt {
      display: unset;
      margin-left: 10px;
      font-size: 14px;
      -webkit-text-stroke-width: 0;
      -webkit-text-fill-color: #fff; }
    .project-card.rafaelbolano, .project-card.foodlab, .project-card.otaku {
      align-items: flex-start;
      left: 0; }
      .project-card.rafaelbolano img, .project-card.foodlab img, .project-card.otaku img {
        display: none; }
      .project-card.rafaelbolano h4, .project-card.foodlab h4, .project-card.otaku h4 {
        margin: 0;
        margin-top: -45px;
        margin-left: 10vw; }
    .project-card.zest, .project-card.santorini, .project-card.thatsmyrock {
      left: 0; }
      .project-card.zest img, .project-card.santorini img, .project-card.thatsmyrock img {
        display: none; }
      .project-card.zest h3, .project-card.santorini h3, .project-card.thatsmyrock h3 {
        margin-left: 10vw; }
      .project-card.zest h4, .project-card.santorini h4, .project-card.thatsmyrock h4 {
        margin-top: -45px;
        margin-left: 10vw; }
    .project-card h3 {
      margin-left: 10vw;
      font-size: 42px; }
    .project-card h4 {
      margin-top: -50px; } }
