section#about {
  height: 100%;
  width: 100vw;
  display: flex;
  padding: 20% 0;
  z-index: 5;
  line-height: 30px;
  align-items: center;
  justify-content: center;
  font-family: "Montserrat", sans-serif; }
  section#about h5 {
    font-size: 20px; }
    section#about h5:first-child {
      margin-top: 0px; }
  section#about .about-container {
    opacity: 0;
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    width: 80%; }
    section#about .about-container .intro {
      font-weight: 900;
      font-size: 32px; }
    section#about .about-container .bolder {
      font-weight: 900; }
    section#about .about-container ul {
      list-style: square; }
    section#about .about-container .about-column {
      width: 35%; }
      section#about .about-container .about-column.presentation {
        text-align: justify; }
        section#about .about-container .about-column.presentation p:first-child {
          margin-top: 0px; }

@media screen and (max-width: 728px) {
  section#about .about-container {
    flex-direction: column;
    width: 80%; }
    section#about .about-container .about-column {
      width: 100%; } }
