header {
  position: relative;
  width: 92%;
  height: 13vh;
  position: fixed;
  display: flex;
  z-index: 999;
  font-size: 14px;
  align-items: center;
  justify-content: space-between; }
  header .logo {
    cursor: pointer;
    position: absolute; }
  header .mobile-only {
    display: none; }
  header .center-menu {
    max-width: 20%;
    height: 30px;
    margin-left: 48%;
    display: flex;
    align-items: center;
    justify-content: center; }
    header .center-menu .language-menu {
      padding-left: 16px;
      margin-left: 16px;
      font-family: "Montserrat", sans-serif; }
      header .center-menu .language-menu span {
        height: 100%;
        margin-right: 16px;
        cursor: pointer; }
        header .center-menu .language-menu span.bolder {
          font-weight: 900; }
    header .center-menu .theme {
      position: relative;
      overflow: hidden;
      height: 16px;
      width: 16px;
      cursor: pointer; }
      header .center-menu .theme .fa-moon {
        position: absolute;
        top: 3px;
        right: 0px;
        animation: 1s moonDown; }
      header .center-menu .theme .fa-sun {
        position: absolute;
        top: 3px;
        right: 0px;
        animation: 1s sunRise; }
  header nav {
    cursor: pointer; }
    header nav .fa-bars {
      margin-left: 5px; }
  header ul {
    position: absolute;
    right: 0;
    top: 50px;
    list-style: none;
    text-align: right; }
    header ul a {
      text-decoration: none; }
    header ul li {
      padding: 10px 0;
      margin: 5px 0;
      cursor: pointer; }
      header ul li span {
        font-family: "Montserrat", sans-serif; }
        header ul li span:first-child {
          margin-right: 10px; }
        header ul li span.bolder {
          font-weight: 900; }

@keyframes moonDown {
  from {
    top: -20px; }
  to {
    top: 2; } }

@keyframes sunRise {
  from {
    top: 20px; }
  to {
    top: 0; } }

@media screen and (max-width: 728px) {
  header .logo {
    position: relative; }
  header .center-menu {
    display: none; }
  header .mobile-only {
    display: block; } }
