.cursor-small {
  position: fixed;
  width: 20px;
  height: 20px;
  top: 0;
  left: 0;
  background-color: #fe1000b8;
  border-radius: 80%;
  backface-visibility: hidden;
  pointer-events: none;
  z-index: 999;
  mix-blend-mode: difference; }

@media screen and (max-width: 728px) {
  .cursor-small {
    display: none; } }

.app {
  position: relative;
  width: 100vw;
  cursor: none;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  align-items: center; }

@media screen and (max-width: 728px) {
  .app {
    cursor: initial; } }
