section#contact {
  height: 100vh;
  width: 100vw;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  font-family: "Montserrat", sans-serif; }
  section#contact .contact-container {
    opacity: 0; }
  section#contact .social-icons {
    display: flex;
    align-items: flex-start;
    justify-content: flex-start;
    width: 70vw;
    margin-top: 50px;
    font-size: 24px; }
    section#contact .social-icons a {
      color: white; }
    section#contact .social-icons i {
      margin-right: 20px; }
  section#contact .contact-infos {
    width: 70vw; }
    section#contact .contact-infos .text-wrapper {
      display: flex;
      height: 5vh;
      font-size: 24px;
      margin-bottom: 10vh; }
    section#contact .contact-infos .mail-wrapper {
      display: flex;
      align-items: center; }
      section#contact .contact-infos .mail-wrapper img {
        border-radius: 50%;
        margin-right: 40px; }
    section#contact .contact-infos .send-email {
      font-size: 14px;
      margin: 0; }
    section#contact .contact-infos .disponibility {
      font-weight: 900;
      margin: 10vh 0; }
    section#contact .contact-infos a {
      color: #fff;
      font-size: 48px; }

.transition-on {
  animation: 2s fadeIn;
  animation-delay: 0.2s;
  animation-fill-mode: forwards; }

@keyframes fadeIn {
  from {
    margin-top: 20vh;
    opacity: 0; }
  to {
    margin-top: 0;
    opacity: 1; } }

@media screen and (max-width: 728px) {
  section#contact {
    height: 100%;
    padding: 20% 0;
    text-align: center; }
    section#contact .social-icons {
      width: 100vw;
      align-items: center;
      justify-content: center; }
      section#contact .social-icons a:last-child i {
        margin-right: 0; }
    section#contact .contact-infos {
      width: 100%; }
      section#contact .contact-infos .disponibility {
        margin: 10vh; }
      section#contact .contact-infos .text-wrapper {
        margin-left: 5vw;
        margin-right: 5vw; }
      section#contact .contact-infos .mail-wrapper {
        flex-direction: column; }
        section#contact .contact-infos .mail-wrapper img {
          margin-right: 0;
          margin-bottom: 30px; }
      section#contact .contact-infos a {
        font-size: 18px; } }
